// Custom
class Language {
    constructor() {
        this.default = this.selected = '';
        this.list = [];
        (async () => {
            const response = await fetch(`${process.env.PUBLIC_URL}/localization.json`);
            this.localization = await response.json();
            this.default = this.selected = this.localization.default;
            this.list = this.localization.all;
            const response2 = await fetch(`${process.env.PUBLIC_URL}/localization/${this.selected}.json`);
            this.defaultLang = await response2.json();
        })();
    }
    
    async load(code) {
        while (!this.localization) await null;
        this.selected = code || this.localization.default;
        try {
            const response = await fetch(`${process.env.PUBLIC_URL}/localization/${this.selected}.json`);
            return { ...this.defaultLang, ...await response.json() };
        } catch (error) {
            return this.defaultLang;
        }
    }

    name(code) {
        const s = LocaleCode.getLanguageNativeName(code);
        return s ? s[0].toUpperCase() + s.slice(1) : code;
    }
}

const languages = new Language();

export { languages };